
import {defineComponent} from "vue";

export default defineComponent({
  name: "CCard",
  props: {
    elevated: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    className(): string {
      let c = "c-card ";
      if(this.elevated) c += "c-card-elevated";
      else c += "c-card-laid";
      return c;
    }
  }
});
