<template>
  <div :class="className">
    <slot/>
  </div>
</template>

<script lang="ts">
import {defineComponent} from "vue";

export default defineComponent({
  name: "CCard",
  props: {
    elevated: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    className(): string {
      let c = "c-card ";
      if(this.elevated) c += "c-card-elevated";
      else c += "c-card-laid";
      return c;
    }
  }
});
</script>

<style lang="scss">
.c-card {
  background: white;
  border-radius: 16px;
  color: black;
  overflow: hidden;
}

.c-card-elevated {
  //filter: drop-shadow(0 5px 5px rgba(0, 0, 0, 0.2)) drop-shadow(0 8px 10px rgba(0, 0, 0, 0.14)) drop-shadow(0 3px 14px rgba(0, 0, 0, .12));
  //box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2), 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, .12);
  box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2);
}

.c-card-laid {
  //filter: drop-shadow(0 3px 1px rgba(0, 0, 0, 0.2)) drop-shadow(0 2px 2px rgba(0, 0, 0, 0.14)) drop-shadow(0 1px 5px rgba(0, 0, 0, .12));
  //box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, .12);
  box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2);
}
</style>